<template>
  <div>
    <label>
      <el-button
        icon="el-icon-video-play"
        @click.prevent="playSound()"
        :disabled="dis1"
        circle
      ></el-button>
      Play the music clip
      <el-button type="danger" icon="el-icon-video-pause" circle @click="stop" :disabled="dis2 || !dis1"></el-button>
    </label>
  </div>
</template>


<script>
export default {
  name: "MusicPlayer",
  props: {
    previewURL: String,
    questionIndex: Number,
  },
  data() {
    return {
      dis1: false,
      audio: {},
      play: true,
      dis2: false,
    };
  },
  methods: {
    playSound() {
      this.audio.play();
      this.dis1 = true;
    },
    stop() {
      this.audio.pause();
      this.dis2 = true;
    },
  },
  beforeMount() {
    this.audio = new Audio(this.previewURL);
  },

};
</script>
