<script>
import MusicPlayer from "@/components/MusicPlayer.vue";
import AnswerSelector from "@/components/AnswerSelector.vue";

export default {
  name: "Playground",
  data() {
    return {
      options: [],
      previewUrls: [],
      questionIndex: -1,
      msg:
        "<br>Please remember to stop playing by clicking the red button below. <br>Or the music will not stop within 30 seconds. <br>They really like to show off.",
      fullscreenLoading: false,
      loaded: false,
      q1: [],
      q2: [],
      q3: [],
      q4: [],
      q5: [],
      answers: [],
      score: 'Loading',
    };
  },
  components: {
    MusicPlayer,
    AnswerSelector,
  },
  methods: {
    nextQuestion() {
      this.questionIndex += 1;
    },
    openFullScreen1() {
      this.fullscreenLoading = true;
    },
    helper() {
      this.openFullScreen1();
    },
    helper2(){
      this.getScore();
      this.nextQuestion();
    },
    checkloaded() {
      if (this.options.length != 0) {
        //console.log("loaded");
        this.loaded = true;
        this.fullscreenLoading = false;
        if (this.questionIndex == -1) {
          this.nextQuestion();
        }
      }
    },
    childByValue1(childValue){
      this.q1.push(childValue);
    },
    childByValue2(childValue){
      this.q2.push(childValue);
    },
    childByValue3(childValue){
      this.q3.push(childValue);
    },
    childByValue4(childValue){
      this.q4.push(childValue);
    },
    childByValue5(childValue){
      this.q5.push(childValue);
    },
    getScore(){
      this.answers.push({previewUrl: this.previewUrls[0], trackName: this.options[0][this.q1[this.q1.length - 1] - 1]});
      this.answers.push({previewUrl: this.previewUrls[1], trackName: this.options[1][this.q2[this.q2.length - 1] - 1]});
      this.answers.push({previewUrl: this.previewUrls[2], trackName: this.options[2][this.q3[this.q3.length - 1] - 1]});
      this.answers.push({previewUrl: this.previewUrls[3], trackName: this.options[3][this.q4[this.q4.length - 1] - 1]});
      this.answers.push({previewUrl: this.previewUrls[4], trackName: this.options[4][this.q5[this.q5.length - 1] - 1]});
      var temp = {answers: this.answers};
      this.$http.post(
        process.env.BASE_URL + "game/answers",
        JSON.stringify(temp),
        { emulateJSON: true }
      ).then( res => {
        this.score = res.body[0].score * 20;
        
      });
    },
    again(){
      this.previewUrls = [];
      this.options = [];
      this.questionIndex = -1;
      this.getSongs();
      this.score = "loading";
      this.loaded = false;
      this.answers = [];
      this.q1 = [];
      this.q2 = [];
      this.q3 = [];
      this.q4 = [];
      this.q5 = [];
    },
    getSongs(){
      this.$http
      .get(process.env.BASE_URL + "game/questions")
      .then((res) => res.json())
      .then((data) => {
        this.options[0] = data[0].trackNames;
        this.previewUrls[0] = data[0].previewUrl;
        this.options[1] = data[2].trackNames;
        this.previewUrls[1] = data[2].previewUrl;
        this.options[2] = data[3].trackNames;
        this.previewUrls[2] = data[3].previewUrl;
        this.options[3] = data[4].trackNames;
        this.previewUrls[3] = data[4].previewUrl;
        this.options[4] = data[1].trackNames;
        this.previewUrls[4] = data[1].previewUrl;
      });
    },
  },
  created() {
    this.getSongs();
    this.interval = setInterval(() => this.checkloaded(), 1000);
  },
};
</script>

